/* General styles */
a {
  text-decoration: none;
  font-weight: bold;
  color: #006600;
}

a:hover{
  color: #00cc00;
}

/* Main body */
body {
  background-color: #003399;
}

/* Europe Ian title */
.title {
  text-align: center;
  margin-bottom: 2vh;
}

.title img {
  width: 30vw;
}

/* Interactive map of Europe */
.mapWrapper {
  max-width: 60vw;
  margin-left: auto;
  margin-right: auto;
}

.europeMap {
  background-image: url('/images/EuropeMap.png');
  background-size: cover;
  padding-bottom: 72.1%;
  position: relative;
}

.cityLabel {
  position: absolute;
  top: 55%;
  left: 62%;
}



